 .container {
   width: 100%;
   height: 100%;
   padding: 0px 15px 0px 15px;
   margin: 0px auto;

                  
        @include screen("mobi") {
          width: 480px;
                
          
        }

        @include screen("tablet") {
          width: 640px;

                  .registration-repeat &{
                              @include aligh(flex, center);
                              flex-direction: column;             
                }
        }

        

        @include screen("desktop") {
          width: 1200px;

                  .free-registration & {
                              display: flex;
                              margin: 0px;
                              padding: 0px;
                              width: 100%;
                }

                  .navigation & {
                              justify-content: center;
                }

                  .other-products &{
                            @include aligh(flex, center);
                            flex-direction: column;
                            justify-content: center;
                }
                  
                  
        }

 }